import {GButton} from "../../../styles/components/GButton";
import Global from "src/system/Global";
import {Link, useHistory} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  exploreSidebarContainer: {
    width: "100%",
    height: "inherit",
    paddingLeft: 33,
    "&::-webkit-scrollbar": {
      display: "none",
    },
    "&-ms-overflow-style": "none",
    scrollbarWidth: "none",
    "& .sidebar-body": {
      maxWidth: 350,
      "& .sidebar-section": {
        backgroundColor: theme.input.background,
        borderRadius: 10,
        "& .section-header": {
          padding: "16px 15px",
          fontSize: 20,
          fontWeight: 800,
          borderBottom: theme.notchedOutline.border,
        },
        "& .section-content": {
          "& .sub-section": {
            padding: "15px 15px",
            "&:not(:last-child)": {
              borderBottom: theme.notchedOutline.border,
            },
          },
        },
      },
    },
    "@media (max-width: 1365px)": {
      maxWidth: 360,
      boxSizing: "content-box",
      paddingLeft: 23,
      width: "calc(100% - 23px)",
      "& .sidebar-body": {
        width: "100%",
        paddingRight: 5,
      },
    },
  },
  wrapperFooter: {
    fontSize: "12px",
    fontWeight: theme.typography.fontWeightMedium,
    letterSpacing: "0.01em",
    paddingBottom: "25px",
    color: theme.palette.text.gray,
    "& .link-list": {
      marginBottom: 3,
    },
    "& .copy-right": {
      fontWeight: 400,
    },
    [theme.breakpoints.down("md")]: {
      width: "100%",
      justifyContent: "center",
    },
    "& a": {
      marginRight: "10px",
      textDecoration: "none",
      letterSpacing: "0.01em",
      fontWeight: 500,
      color: theme.palette.text.gray,
      lineHeight: "190%",
    },
  },
  newToGetter: {
    border: `1px solid ${theme.palette.grey["A800"]}`,
    boxSizing: "border-box",
    borderRadius: 14,
    padding: "14px 10px",
    marginBottom: "15px",
    "& .title": {
      fontSize: "19px",
      fontWeight: "800",
      color: theme.palette.text.main,
      marginLeft: "6px",
    },
    "& .description": {
      fontSize: "12px",
      lineHeight: "14px",
      color: theme.palette.secondary.gray,
      padding: "15px 0",
    },
  },
  signUpButton: {
    borderRadius: "100px",
    padding: "11px 0",
    "& span": {
      fontSize: "14px !important",
      lineHeight: "17px !important",
    },
  },
  stickySearchBoxWrapper: {
    "& > .sticky-inner-wrapper": {
      zIndex: 2,
      backgroundColor: theme.palette.background.default,
    },
  },
}));

export const NewExploreSideBar = () => {
  const classes = useStyles();
  const history = useHistory();

  const api = Global.GetPortal().getAppService();
  const userId = api.getUserId();
  const token = api.getUserToken();
  const loggedIn = !!userId;
  const registerUrl = api.getUrlSignup();

  return (
    <div className={classes.exploreSidebarContainer}>
      <Sticky enableTransforms={false}>
        <div className="sidebar-body">
          <div className="inner-wrapper">
            <Sticky
              enableTransforms={false}
              className={classes.stickySearchBoxWrapper}
            >
              <PostSearchBoxNew />
            </Sticky>
            {!loggedIn && (
              <div className={classes.newToGetter}>
                <div className="title">
                  {t("getter_fe.auth.common.newToGetter")}
                </div>
                <div className="description">
                  {t("getter_fe.auth.common.newToGetterTips")}
                </div>
                <GButton
                  isBold
                  color="primary"
                  variant="contained"
                  className={classes.signUpButton}
                  onClick={() => history.push(registerUrl)}
                >
                  {t("getter_fe.auth.common.createAccount")}
                </GButton>
              </div>
            )}
            <NewSuggestTopics />
            <SuggestPeople userId={userId} token={token} />
            <div className={classes.wrapperFooter}>
              <div className="link-list">
                <Link className="link text-link" to="/terms">
                  {t("getter_fe.helpCenter.common.termsOfService")}
                </Link>
                <Link className="link text-link" to="/privacy">
                  {t("getter_fe.helpCenter.common.privacyPolicy")}
                </Link>
              </div>
              <div dir="auto" className="copy-right">
                <span>© 2021 GETTR, Inc.</span>
              </div>
            </div>
          </div>
        </div>
      </Sticky>
    </div>
  );
};
