import React, {useState, useEffect} from "react";
import clsx from "clsx";
import {
  makeStyles,
  useMediaQuery,
  createStyles,
  BottomNavigation,
  BottomNavigationAction,
} from "@material-ui/core";
import {useTheme} from "@material-ui/core/styles";
import {useHistory} from "react-router-dom";
import {ReactComponent as MobileHomeIcon} from "src/assets/icons/ico_mobile_home.svg";
import {ReactComponent as ActivatedMobileHomeIcon} from "src/assets/icons/ico_mobile_home_activated.svg";
import {ReactComponent as MobileAccountIcon} from "src/assets/icons/ico_mobile_account.svg";
import {ReactComponent as ActivatedMobileAccountIcon} from "src/assets/icons/ico_mobile_account_activated.svg";
import {ReactComponent as MobileSearchIcon} from "src/assets/icons/ico_mobile_search.svg";
import {ReactComponent as ActivatedMobileSearchIcon} from "src/assets/icons/ico_mobile_search_activated.svg";
import {t} from "src/i18n/utils";

import AppConsts from "src/app/AppConsts";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      position: "fixed",
      height: 60,
      bottom: 0,
      width: "100%",
      borderTop: `0.5px solid ${theme.palette.grey.A200}`,
      "& .MuiBottomNavigationAction-label": {
        fontSize: 11,
        "& .Mui-selected": {
          fontSize: 11,
        },
      },
      "& .MuiBottomNavigationAction-root": {
        padding: "12px 12px 8px",
      },
    },
  }),
);

export const MobileBottomNavigation = ({userId}) => {
  const classes = useStyles();
  const [value, setValue] = useState(0);
  const history = useHistory();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.only("xs"));
  const currentPathname = window.location.pathname;

  const isSearchPage = () => {
    return (
      currentPathname.includes("/search?q=") ||
      currentPathname.includes("/search") ||
      currentPathname.includes("/hashtag/")
    );
  };

  const hideNavigation = () => {
    return (
      currentPathname.includes("/terms-app") ||
      currentPathname.includes("/privacy-app") ||
      currentPathname.includes("/cookie-policy-app") ||
      currentPathname.includes("/login") ||
      currentPathname.includes("/signup") ||
      currentPathname.includes("/claim")
    );
  };

  useEffect(() => {
    const location = currentPathname;

    if (
      location === `/user/${userId}` ||
      location === "/account" ||
      location.includes(`/user/${userId}`)
    ) {
      setValue(2);
    } else if (isSearchPage()) {
      setValue(1);
    } else {
      setValue(0);
    }
  }, [currentPathname]);

  if (currentPathname.includes(AppConsts.URL_HELP_CENTER_MOBILE_APP))
    return null;

  return (
    matches &&
    !hideNavigation() && (
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
          switch (newValue) {
            case 0:
              history.push("/");
              break;
            case 1:
              if (!isSearchPage()) {
                history.push("/search?&tab=0");
              }
              break;
            case 2:
              if (!userId) {
                history.push("/login");
              } else {
                history.push(`/account`);
              }
              break;
            default:
              history.push("/");
              break;
          }
        }}
        showLabels
        className={clsx(classes.root, "mobile-nav")}
      >
        <BottomNavigationAction
          label={t("getter_fe.menu.common.home")}
          icon={value === 0 ? <ActivatedMobileHomeIcon /> : <MobileHomeIcon />}
        />
        <BottomNavigationAction
          label={t("getter_fe.menu.common.search")}
          icon={
            value === 1 ? <ActivatedMobileSearchIcon /> : <MobileSearchIcon />
          }
        ></BottomNavigationAction>
        <BottomNavigationAction
          label={t("getter_fe.menu.common.account")}
          icon={
            value === 2 ? <ActivatedMobileAccountIcon /> : <MobileAccountIcon />
          }
        />
      </BottomNavigation>
    )
  );
};
