import React, {useState, useEffect, useCallback} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import {useHistory} from "react-router-dom";
import clsx from "clsx";

import {t, getLang} from "../../i18n/utils";
import {Loader} from "src/app/pages/settings/Loader";
import axios from "axios";
import {UserLayout} from "./UserLayout";
import {GTypography} from "src/styles/components/GTypography";
import Global from "src/system/Global";
import {
  HDR_APP_AUTH,
  PROP_USER_ID,
  PROP_USER_TOKEN,
} from "src/core/model/net/XRequest";

const useStyles = makeStyles((theme) =>
  createStyles({
    border: {
      backgroundColor: theme.palette.grey.A200,
      height: "11px",
    },
    suggestPeopleContainer: {
      marginTop: 11,
      marginBottom: 11,
    },
    sectionHeader: {
      padding: "16px 15px",
      fontSize: 20,
      fontWeight: 800,
      borderBottom: theme.notchedOutline.border,
      "&.zh,&.tw": {
        fontWeight: "600 !important",
        letterSpacing: "0.05em",
      },
    },
    wrapper: {
      height: "100%",
    },
    cardWrapper: {
      height: "100%",
    },
    footerContent: {
      padding: "16px 5px",
      marginLeft: theme.spacing(1.875),
      display: "flex",
      alignItems: "center",
      height: theme.spacing(5.435),
    },
    footerText: {
      cursor: "pointer",
      color: theme.blue.light,
      fontWeight: 600,
      "&.zh,&.tw": {
        fontWeight: 500,
        letterSpacing: "0.05em",
      },
    },
  }),
);

export const WhoToFollow = ({isBottom}) => {
  const classes = useStyles();
  const maxPerPage = 3;
  const [items, setItems] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const api = Global.GetPortal().getAppService();
  let token = api.getUserToken();
  let userId = api.getUserId();

  // JoeC 2021-03-31: Why is axios code here instead of calling GetterService.fetchSuggestedUsertags()?
  // userId and token are null when user are not logged in and this is handled in XRequest/REST
  // let headerProps = {};
  // if (userId != null || token != null) {
  //   let authLine = {};
  //   if (userId != null) {
  //     authLine[PROP_USER_ID] = userId;
  //   }
  //   if (token != null) {
  //     authLine[PROP_USER_TOKEN] = token;
  //   }
  //   headerProps[HDR_APP_AUTH] = authLine;
  // }

  const fetchData = useCallback(async () => {
    let dataArr = [];
    let xAuth =
      userId === null
        ? `{"user": null, "token": null}`
        : `{"user": "${userId}", "token": "${token}"}`;
    setIsLoading(true);
    await axios
      .get(
        `${process.env.REACT_APP_API_URL}/s/usertag/suggest?offset=0&max=${maxPerPage}&incl=userinfo|followings`,
        {
          headers: {"x-app-auth": xAuth},
        },
      )
      .then((res) => {
        const data = res.data?.result?.aux?.uinf;
        const followings = res.data?.result?.aux?.fws;

        if (data) {
          for (const [key, value] of Object.entries(data)) {
            if (value?._id && followings.includes(value?._id)) {
              value.isFollowing = true;
            } else {
              value.isFollowing = false;
            }
            dataArr.push(value);
          }
        }
      })
      .catch((err) => {
        console.error(err);
      });

    setItems([...items, ...dataArr]);

    setIsLoading(false);
  });

  useEffect(() => {
    items.length <= 0 && fetchData();
  }, []);

  const gotoPopularUsersList = () => {
    history.push("/popular-users");
  };

  return (
    <>
      <div className={classes.border}></div>
      <div className={clsx(classes.suggestPeopleContainer, "sidebar-section")}>
        <div
          className={clsx("section-header", classes.sectionHeader, getLang())}
        >
          {t("getter_fe.common.people.whoToFollow")}
        </div>
        <div className="section-content">
          <div className={classes.wrapper}>
            {!isLoading && items.length <= 0 && ""}

            <div className={!isLoading ? classes.cardWrapper : ""}>
              {items &&
                items.map((user, idx) => {
                  return (
                    <UserLayout
                      key={`pul-${idx}`}
                      userInfo={user}
                      sidebar={true}
                      isPage={null}
                    />
                  );
                })}
              {items.length ? (
                <div className={classes.footerContent}>
                  <GTypography
                    className={clsx(classes.footerText, getLang())}
                    isBold
                    variant="body2"
                    onClick={gotoPopularUsersList}
                  >
                    {t("getter_fe.common.trends.viewMore")}
                  </GTypography>
                </div>
              ) : null}
            </div>

            {isLoading && <Loader />}
          </div>
        </div>
      </div>
      {!isBottom ? <div className={classes.border}></div> : null}
    </>
  );
};
