import {t, getLang} from "src/i18n/utils";
import {GTypography} from "src/styles/components/GTypography";
import {TopicImg} from "./TopicImg";

const useStyles = makeStyles((theme) =>
  createStyles({
    sectionHeader: {
      borderBottom: `1px solid ${theme.palette.grey["A800"]}`,
      height: "45px",
      fontWeight: "800",
      lineHeight: "24px",
      padding: "12px 15px",
      fontSize: "20px",
    },
    topicContent: {
      display: "flex",
      borderBottom: `1px solid ${theme.palette.grey["A800"]}`,
      padding: "12px 15px",
      "&:hover": {
        backgroundColor: theme.palette.grey["A300"],
      },
    },
    topicName: {
      fontWeight: "700",
      lineHeight: "19.5px",
      fontSize: "15px",
      overflow: "hidden",
      textOverflow: "ellipsis",
    },
    topicDesc: {
      marginLeft: "14px",
      marginRight: "15px",
      marginTop: "3px",
      fontWeight: "400",
      lineHeight: "17px",
      fontSize: "13px",
      "& .text-content": {
        display: "-webkit-box",
        "-webkit-line-clamp": 2,
        "-webkit-box-orient": "vertical",
        overflow: "hidden",
      },
    },
    textContent: {
      flexGrow: 1,
      fontWeight: 700,
      lineHeight: "19.5px",
      fontSize: "15px",
      width: "65%",
      "& .text-content": {
        display: "-webkit-box",
        "-webkit-line-clamp": 2,
        "-webkit-box-orient": "vertical",
        overflow: "hidden",
        wordBreak: "break-word",
        fontSize: 13,
      },
      "& .grey": {
        color: theme.palette.secondary.gray,
        fontWeight: 400,
      },
    },
    img: {
      borderRadius: "10px",
      width: "69px",
      height: "69px",
      marginLeft: "12px",
      objectFit: "cover",
    },
    sectionFooter: {
      display: "flex",
      alignItems: "center",
      height: 45,
      color: theme.palette.primary.main,
      cursor: "pointer",
      "&:hover": {
        backgroundColor: theme.palette.grey["A300"],
        borderRadius: "0 0 10px 10px",
      },
    },
    footerContent: {
      marginLeft: 15,
    },
    footerText: {
      color: theme.blue.light,
      position: "relative",
      fontWeight: 600,
      "&.zh,&.tw": {
        fontWeight: 500,
        letterSpacing: "0.05em",
      },
      "& .underline": {
        position: "absolute",
        left: 0,
        bottom: 2,
        width: "100%",
        height: 1,
        backgroundColor: theme.blue.light,
        opacity: 0,
        transition: "opacity 300ms ease",
      },
      "&:hover .underline": {
        opacity: 1,
      },
    },
  }),
);

export const NewSuggestTopics = () => {
  const classes = useStyles();
  const history = useHistory();
  // const portal = Global.GetPortal();
  // const api = portal.getAppService();
  const [topics, setTopics] = useState(null);
  const [error, setError] = useState(false);

  useEffect(() => {
    // api.fetchSuggestedHashtags(0, 5, (err, resultList) => {
    //   if (resultList) {
    //     console.log(resultList);
    //     setTopics(resultList)
    //   }
    // });
    const config = {
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/s/hashtag/suggest?max=5`,
    };

    GAxios(
      config,
      (response) => {
        if (response && response.data?.result?.aux?.htinfo) {
          setTopics(response.data?.result?.aux?.htinfo);
        } else {
          setError(true);
        }
      },
      () => {
        setError(true);
      },
    );
  }, []);

  if (error) {
    return null;
  }

  return (
    <div className="suggest-topics sidebar-section">
      <div className={classes.sectionHeader}>
        {t("getter_fe.common.trends.title")}
      </div>
      <div className="section-content">
        {topics &&
          Object.keys(topics).map((key, index) => {
            const data = topics[key];
            const trendDesc = data["description"];

            let tagname = key;

            return (
              <Link
                to={`/search?q=${encodeURIComponent(tagname)}`}
                key={"st" + index}
                className={classes.topicContent}
              >
                <div className={classes.textContent}>
                  <div className={classes.topicName}>{tagname}</div>
                  <div
                    className={clsx("text-content", trendDesc ? "grey" : "")}
                  >
                    {trendDesc
                      ? trendDesc
                      : t("getter_fe.common.trends.desc", {key})}
                  </div>
                </div>
                <TopicImg url={data["iconUrl"]} />
              </Link>
            );
          })}
      </div>
      <div
        className={classes.sectionFooter}
        onClick={() => history.push("/trending")}
      >
        <div className={classes.footerContent}>
          <GTypography
            className={clsx(classes.footerText, getLang())}
            variant="body2"
          >
            {t("getter_fe.common.trends.viewMore")}
          </GTypography>
        </div>
      </div>
    </div>
  );
};
