import React, {useEffect} from "react";
import PropTypes from "prop-types";
import {makeStyles} from "@material-ui/core/styles";
import {Tabs, Tab, Typography} from "@material-ui/core";
import {t} from "../../../i18n/utils";
import Muted from "./Muted";
import Blocked from "./Blocked";
import {ReactComponent as BackArrowIcon} from "../../../assets/icons/ico-arrow.svg";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import {useTheme} from "@material-ui/core/styles";
import {useHistory} from "react-router-dom";
import AppConsts from "../../AppConsts";
import Global from "src/system/Global";

function TabPanel({children, value, index, ...other}) {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    "aria-controls": `tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  tabTitle: {
    height: 58,
    paddingLeft: theme.spacing(4),
    fontWeight: 800,
    fontSize: 20,
    lineHeight: "22px",
    color: theme.palette.text.primary,
    textTransform: "capitalize",
    borderBottom: `1px solid ${theme.palette.grey.A200}`,
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.down("sm")]: {
      // tablet or mobile
      paddingLeft: theme.spacing(1.5),
      paddingRight: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.grey.A200}`,
      marginTop: 0,
      height: 58,
      lineHeight: "58px",
      fontSize: 18,
      "& > svg": {
        // back arrow icon
        marginRight: theme.spacing(2),
        verticalAlign: "top",
        cursor: "pointer",
        borderRadius: "50%",
        padding: 1,
        width: 26,
        height: 26,
        "&:hover": {
          backgroundColor: "#F2F9FF",
        },
      },
    },
  },
  root: {
    width: "100%",
    height: "100%",
    minHeight: "calc(100vh - 58px)",
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    [theme.breakpoints.down("sm")]: {
      minHeight: `calc(100vh - ${
        58
        // theme.mixins.header.height + tabTitleHeight
      }px)`,
    },
  },
  tabPanel: {
    height: "100%",
  },
  tabs: {
    "& .MuiTabs-indicator": {
      backgroundColor: theme.palette.primary.main,
    },
  },
  tab: {
    color: theme.palette.text.secondary,
    textTransform: "capitalize",
    fontSize: 16,
    lineHeight: "16px",
    letterSpacing: "0.033em",
    opacity: 1,
    fontWeight: 600,
    height: "56px",
    borderBottom: `1px solid ${theme.palette.grey.A200}`,
    "&.Mui-selected": {
      color: theme.palette.primary.main,
    },
    "& .MuiTab-wrapper": {
      opacity: 0.5,
    },
    "&.Mui-selected .MuiTab-wrapper": {
      opacity: 1,
    },
  },
}));

export default function PrivacyTabs({userId, token, setPageSeo}) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const theme = useTheme();
  const isTabletOrMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const history = useHistory();

  const handleChange = (event, newValue) => {
    event.preventDefault();
    setValue(newValue);
  };

  useEffect(() => {
    setPageSeo(t("getter_fe.settings.common.privacy"));
  }, []);

  return (
    <>
      <Typography variant="h1" className={classes.tabTitle}>
        {isTabletOrMobile && (
          <BackArrowIcon
            onClick={() =>
              history.push(`/settings/${AppConsts.URL_SETTINGS_MOBILE_INDEX}`)
            }
          />
        )}
        {t("getter_fe.settings.common.privacy")}
      </Typography>
      <div className={classes.root}>
        <Tabs
          value={value}
          onChange={handleChange}
          variant="fullWidth"
          className={classes.tabs}
        >
          <Tab
            label={t("getter_fe.settings.common.mutedTitle")}
            {...a11yProps(0)}
            className={classes.tab}
            disableRipple={true}
          />
          <Tab
            label={t("getter_fe.settings.common.blockedTitle")}
            {...a11yProps(1)}
            className={classes.tab}
            disableRipple={true}
          />
        </Tabs>
        <TabPanel value={value} index={0} className={classes.tabPanel}>
          <Muted userId={userId} token={token} />
        </TabPanel>
        <TabPanel value={value} index={1} className={classes.tabPanel}>
          <Blocked userId={userId} token={token} />
        </TabPanel>
      </div>
    </>
  );
}
