import {makeStyles} from "@material-ui/core";
import {useEffect, useState} from "react";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import {Waypoint} from "react-waypoint";
import {GLoader} from "src/styles/components/GLoader";
import {CommentFeedItem} from "src/app/components/post/comps/CommentFeedItem";
import {
  fetchUserComments,
  clearUserComments,
  incrementOffset,
} from "src/app/components/comment/store";
import {GTwemoji} from "src/styles/components/GTwemoji";
import {t} from "src/i18n/utils";
import {commentsConstants} from "src/app/components/comment/_constants";
import Global from "src/system/Global";

const useStyles = makeStyles((theme) => ({
  root: {
    marginBottom: theme.spacing(1),
  },
  commentContainer: {
    borderBottom: `1px solid ${theme.palette.grey.A200}`,
  },
  claimTop: {
    width: "100%",
    height: "44px",
    marginBottom: "15px",
  },
  tellTwitter: {
    width: "100%",
    background: "rgba(47, 128, 237, 0.15)",
    alignItems: "center",
    textAlign: "center",
    height: "44px",
    paddingTop: "10px",
  },

  link: {
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "16px",
    lineHeight: "22px",
    color: "#2F80ED",
  },
  sync: {
    width: "100%",
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "16px",
    lineHeight: "22px",
    textAlign: "center",
    marginTop: "40px",
    marginBottom: "8px",
    color: "#333333",
  },
  sync2: {
    width: "100%",
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "16px",
    lineHeight: "22px",
    textAlign: "center",
    marginBottom: "20px",
    color: "#999999",
  },
  noDataWrapper: {
    textAlign: "center",
    marginTop: "32px",
    marginBottom: "32px",
  },
  noDataTips: {
    fontSize: "18px",
    lineHeight: "21px",
    color: "#000",
    marginBottom: theme.spacing(1),
  },
  noDataSubtext: {
    color: theme.palette.text.gray,
  },
}));

const connector = connect(
  (state) => {
    return {
      loggedinUserId: state.auth.session?.userinfo?._id,
      loggedinUserInfo: state.auth.session?.userinfo,
      isLoading: state.userComments.commentFeed.isLoading,
      data: state.userComments.commentFeed.data,
      reachEnd: state.userComments.commentFeed.reachEnd,
      offset: state.userComments.commentFeed.offset,
    };
  },
  {fetchUserComments, clearUserComments, incrementOffset},
);

export const CommentFeed = connector(_CommentFeed);

function _CommentFeed({
  userId,
  loggedinUserId,
  loggedinUserInfo,
  wrapperStyle,
  userNickname,
  isMyProfile,
  isLoading,
  data,
  offset,
  fetchUserComments,
  clearUserComments,
  incrementOffset,
  reachEnd,
}) {
  const classes = useStyles();
  const {MAX_BATCH_SIZE: maxBatchSize} = commentsConstants;
  const [importStatus, setImportStatus] = useState(false);
  const [tellTwitter, setTellTwitter] = useState(false);
  const [endHours, setEndHours] = useState(48);

  const api = Global.GetPortal().getAppService();

  useEffect(() => {
    return () => clearUserComments();
  }, []);

  useEffect(() => {
    // code for keeping comments when other routes are using redux
    // const userCommentId = data[0]?.getPosterId();

    // if (userCommentId && userCommentId !== userId) {
    //   // handles the case where you view a new comment feed
    //   clearUserComments();
    // } else if ((offset + 1) * maxBatchSize === data.length || reachEnd) {
    //   // handles the case where youve left the page and returned to the same comment feed
    //   return;
    // }

    fetchUserComments({
      userId,
      offset,
      maxBatchSize,
      api,
    });
  }, [userId, offset]);

  const fetchAsyncStatus = async () => {
    if (userId === loggedinUserId) {
      let response;
      const config = {
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/u/user/${userId}/import`,
      };

      await GAxios(
        config,
        (res) => {
          if (!res?.data?.result?.status?.finished) {
            setImportStatus(true);
          }
          if (!res.data.result) {
            response = false;
          }
        },
        (err) => {
          console.error(err);
          response = false;
        },
      );
      return response;
    }
  };

  useEffect(() => {
    if (userId === loggedinUserId) {
      let now = Date.now();
      let hours = (now - loggedinUserInfo?.cdate) / (1000 * 60 * 60);
      setEndHours(48 - Math.floor(hours));
      let mins = (now - loggedinUserInfo?.cdate) / (1000 * 60);
      const imPost = localStorage.getItem(userId + "_imPost");

      if (hours < 48 && mins > 1 && imPost) {
        let res = fetchAsyncStatus();
      }

      if (mins < 1 && imPost) {
        setImportStatus(true);
      }
      if (hours < 24) {
        // setTellTwitter(true);
      }
    }
  }, [userId]);

  if (!data.length && isLoading) {
    return <GLoader wrapperStyle={wrapperStyle} />;
  }

  if (!data.length) {
    return (
      <>
        {tellTwitter && !importStatus && (
          <div className={classes.claimTop}>
            <div className={classes.tellTwitter}>
              <Link
                className={classes.link}
                onClick={() => {
                  let text = t("getter_fe.auth.claim.tellTwitterText");
                  let href = window.location.origin + `/user/${userId}`;
                  window.open(
                    `https://twitter.com/intent/tweet?url=${href}&text=${text}`,
                    "_blank",
                  );
                }}
              >
                {t("getter_fe.auth.claim.tellTwitter")}
              </Link>
            </div>
          </div>
        )}
        <div className={classes.noDataWrapper}>
          <h5 className={classes.noDataTips}>
            <GTwemoji
              text={
                isMyProfile
                  ? t("getter_fe.profile.common.youHaventCommented")
                  : t("getter_fe.profile.common.otherHasntCommented", {
                      name: userNickname,
                    })
              }
            />
          </h5>
          <p className={classes.noDataSubtext}>
            {isMyProfile
              ? t("getter_fe.profile.common.youNoPostLikeOrCommentSubtext")
              : t("getter_fe.profile.common.otherNoPostLikeOrCommentSubtext")}
          </p>
        </div>
      </>
    );
  }
  return (
    <div className={classes.root}>
      {tellTwitter && !importStatus && (
        <div className={classes.claimTop}>
          <div className={classes.tellTwitter}>
            <Link
              className={classes.link}
              onClick={() => {
                let text = t("getter_fe.auth.claim.tellTwitterText");
                let href = window.location.origin + `/user/${userId}`;
                window.open(
                  `https://twitter.com/intent/tweet?url=${href}&text=${text}`,
                  "_blank",
                );
              }}
            >
              {t("getter_fe.auth.claim.tellTwitter")}
            </Link>
          </div>
        </div>
      )}
      <div>
        {importStatus && !forMedias && (
          <div className={classes.claimTop}>
            <div className={classes.sync}>
              {t("getter_fe.auth.common.importingMSG1")}
            </div>
            <div className={classes.sync2}>
              {t("getter_fe.auth.common.importingMSG2", {hours: endHours})}
            </div>
          </div>
        )}
      </div>
      {data.map((comment, idx) => (
        <div key={idx} className={classes.commentContainer}>
          <CommentFeedItem comment={comment} />
        </div>
      ))}
      {reachEnd ? null : (
        <Waypoint
          scrollableAncestor={window}
          onEnter={() => {
            if (!isLoading) {
              incrementOffset();
            }
          }}
        />
      )}
    </div>
  );
}
