import React, {useState, useEffect} from "react";
import {createStyles, makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) =>
  createStyles({
    img: {
      borderRadius: "10px",
      width: "69px",
      height: "69px",
      marginLeft: "12px",
      objectFit: "cover",
    },
  }),
);

export const TopicImg = ({url}) => {
  const classes = useStyles();
  const [imgUrl, setImgUrl] = useState(null);

  useEffect(() => {
    let img = new Image();
    img.onload = function () {
      setImgUrl(url);
    };
    img.src = url;
  }, [url]);

  return imgUrl ? <img src={imgUrl} className={classes.img} /> : null;
};
