import React, {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import {useHistory} from "react-router-dom";
import {createStyles, makeStyles} from "@material-ui/core/styles";
import Popover from "@material-ui/core/Popover";

import MenuItem from "./MenuItem";
import {AvatarLink} from "./AvatarLink";
import {GTwemoji} from "src/styles/components/GTwemoji";

import {ReactComponent as KebobMenuIcon} from "../../assets/icons/icon_more_vertical.svg";
import {ReactComponent as ProfileIcon} from "../../assets/icons/profile.svg";
import {ReactComponent as SettingsIcon} from "../../assets/icons/settings.svg";
import {ReactComponent as LogOutIcon} from "../../assets/icons/logout.svg";
import {t} from "../../i18n/utils";
import AppConsts from "../AppConsts";
import {GConfirmAlert} from "src/styles/components/GConfirmAlert";
import clsx from "clsx";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      "& .user-menu-trigger": {
        // width: "calc(100% - 10px)",
        width: "100%",
        position: "relative",
        display: "flex",
        alignItems: "center",
        borderRadius: 100,
        padding: 6,

        "&:hover": {
          background: theme.palette.grey[100],
          backgroundColor: "#EDF7FF",
        },

        "& .avatar": {
          ...theme.mixins.avatar,
          width: 40,
          height: 40,
        },

        "& .user-info": {
          padding: "2px 15px",
          maxWidth: 125,
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          alignItems: "flex-start",

          "& .nickname": {
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
            fontWeight: "bold",
            fontSize: "16px",
            lineHeight: "19px",
            maxWidth: 125,
          },

          "& .username": {
            fontWeight: 500,
            color: theme.palette.text.gray,
            fontSize: 13,
            lineHeight: "17px",
            textAlign: "left",
            "&:before": {
              content: '"@"',
            },
          },
        },

        "& .kebobMenu": {
          position: "absolute",
          right: 6,
          height: "24px",
          width: "24px",
          fill: theme.palette.secondary.gray,
        },
      },
    },
    dropdownContent: {
      cursor: "pointer",
      width: theme.spacing(28),
      borderRadius: 6,
      margin: 0,
      borderColor: "#e3e9ee",
      boxShadow: "0 0 7px #00000023",
      marginTop: theme.spacing(-1),
    },
    leftContentContainer: {
      display: "flex",
    },
    smallDropdownContent: {
      bottom: 6,
      left: 59,
    },
  }),
);

const UserMenu = ({small}) => {
  const history = useHistory();
  const classes = useStyles();
  const {session} = useSelector((state) => state.auth);
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const [userInfo, setUserInfo] = useState(null);

  useEffect(() => {
    if (!session?.authenticated) {
      history.push("/logout");
    }
    setUserInfo(session.userinfo);
  }, [session]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setOpen(true);
  };

  const handleClose = () => {
    setAnchorEl(null);
    setOpen(false);
  };

  const getUserMenuTrigger = () => {
    let avatarUrl = userInfo?.ico;
    const nickname = userInfo?.nickname;
    const username = userInfo?.ousername;
    let comp = (
      <div className="user-menu-trigger">
        <div className={classes.leftContentContainer}>
          <AvatarLink
            avatarUrl={avatarUrl}
            styleClasses="avatar"
            userId={userInfo?._id}
            username={userInfo?.nickname}
            disableHistoryPush={true}
            noRedirect={true}
          />
          {!small && (
            <div className="user-info">
              <div className="nickname">
                <GTwemoji text={nickname || username || "-"} size={18} />
              </div>
              <div className="username">
                <GTwemoji text={username} size={18} />
              </div>
            </div>
          )}
        </div>
        {!small && <KebobMenuIcon className="kebobMenu" />}
      </div>
    );

    return comp;
  };

  const goUrl = (e, url = null) => {
    e.preventDefault();

    const nickName = userInfo?.nickname || userInfo?._id;
    handleClose();

    if (AppConsts.URL_LOGOUT === url) {
      GConfirmAlert({
        title: nickName,
        text: t("getter_fe.auth.common.logOutConfirm"),
        close: {
          text: t("getter_fe.post.button.cancel"),
          type: "default",
        },
        confirm: {
          text: t("getter_fe.menu.common.logout"),
          type: "danger",
          callback: (close) => {
            if (url != null) history.push(url);
            close();
            // toast.info(
            //   <NotifMessage
            //     message={t("getter_fe.auth.common.youLoggedOut")}
            //   />,
            //   {
            //     type: toast.TYPE.SUCCESS,
            //   },
            // );
          },
        },
        showCloseIcon: false,
      });
      return;
    }

    if (url != null) history.push(url);
  };

  return (
    <div className={clsx(classes.root, "dropdown")}>
      <span
        aria-describedby="user-menu"
        variant="contained"
        color="primary"
        onClick={handleClick}
      >
        {getUserMenuTrigger()}
      </span>
      <Popover
        id="user-menu"
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        classes={{
          paper: classes.dropdownContent,
        }}
      >
        <MenuItem
          text={t("getter_fe.menu.common.profile")}
          icon={<ProfileIcon />}
          doClick={goUrl}
          url={`/user/${userInfo?._id}`}
        />
        <MenuItem
          text={t("getter_fe.menu.common.settings")}
          icon={<SettingsIcon />}
          doClick={goUrl}
          url={`/settings/change-password`}
        />
        <MenuItem
          text={t("getter_fe.menu.common.logout")}
          icon={<LogOutIcon />}
          doClick={goUrl}
          url={AppConsts.URL_LOGOUT}
        />
      </Popover>
    </div>
  );
};

export default UserMenu;
