  //     this.refreshView();
  //   }
  // };

  /**
   * Given a label (url pathname portion), determine
   * user is currently in this section.
   *
   * @param {string} label section name (e.g., "explore", "notifications", "messages", etc)
   * @return {boolean} true if in section specified by label
   */
  // const isSection = (label) => {
  //   let path = getCurrentPath();

  //   let match = label[0] !== "/" ? "/" + label : label;

  //   // for now, stupid logic to match anywhere in url string
