  //   // if (userInfo == null) userInfo = getUserInfo(); // for logged in user
  //   return userInfo;
  // };

  /**
   * This should belong in its own component
   *
  //  */
  // const renderUserSummary = () => {
  //   let userInfo = getCurrentUserInfo();

  //   if (userInfo != null) {
  //     let profileNickname = userInfo.getNickname();
  //     // let userId = userInfo.getUserId();
  //     let profileUsername = userInfo.getUsername("Not Logged In");
  //     let userStats = this.loadUserStats(null, this.notifyUpdatedUserStats);

  //     // let userId = obj.getUserId();
  //     let objDesc = userInfo.getDescription();
  //     if (objDesc == null) objDesc = "[No information]";

  //     let userLikes = userStats ? userStats.getLikesCount(0) : 0;
  //     let followsCount = userStats ? userStats.getFollowsCount(0) : 0;
  //     let followersCount = userStats ? userStats.getFollowersCount(0) : 0;
  //     let socialStats = (
  //       <React.Fragment>
  //         <span className="stats">Followers: {followersCount}</span>
  //         <br />
  //         <span className="stats">Followings: {followsCount}</span>
  //         <br />
  //         <span className="stats">Likes: {userLikes}</span>
  //       </React.Fragment>
  //     );

  //     let userSummary = userLoggedIn ? (
  //       <span>
  //         <span className="name">
  //           <b>{profileNickname}</b> (@{profileUsername})
  //         </span>
