import {makeStyles} from "@material-ui/core";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import {useEffect, useState} from "react";
import {useDispatch} from "react-redux";
import {Waypoint} from "react-waypoint";
import API from "src/core/API";
import {t} from "src/i18n/utils";
import {setPostStatus} from "src/app/components/timeline/store";
import {GLoader} from "src/styles/components/GLoader";
import {CommentFeedItem} from "src/app/components/post/comps/CommentFeedItem";
import {PostFeedItem} from "src/app/components/post/comps/PostFeedItem";
import {GTwemoji} from "src/styles/components/GTwemoji";
import {postConstants} from "src/app/components/post/_constants";
import Global from "src/system/Global";

const useStyles = makeStyles((theme) => ({
  root: {
    marginBottom: theme.spacing(1),
  },
  commentContainer: {
    borderBottom: `1px solid ${theme.palette.grey.A200}`,
  },
  claimTop: {
    width: "100%",
    height: "44px",
    marginBottom: "15px",
  },
  tellTwitter: {
    width: "100%",
    background: "rgba(47, 128, 237, 0.15)",
    alignItems: "center",
    textAlign: "center",
    height: "44px",
    paddingTop: "10px",
  },
  link: {
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "16px",
    lineHeight: "22px",
    color: "#2F80ED",
  },
  sync: {
    width: "100%",
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "16px",
    lineHeight: "22px",
    textAlign: "center",
    marginTop: "40px",
    marginBottom: "8px",
    color: "#333333",
  },
  sync2: {
    width: "100%",
    fontFamily: "PingFang SC",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "16px",
    lineHeight: "22px",
    textAlign: "center",
    marginBottom: "20px",
    color: "#999999",
  },
  noDataWrapper: {
    textAlign: "center",
    marginTop: "32px",
    marginBottom: "32px",
  },
  noDataTips: {
    fontSize: "18px",
    lineHeight: "21px",
    color: "#000",
    marginBottom: theme.spacing(1),
  },
  noDataSubtext: {
    color: theme.palette.text.gray,
  },
}));

const MAX_BATCH_SIZE = 20;

const connector = connect((state) => {
  return {
    loggedinUserInfo: state.auth.session?.userinfo,
    authenticated: state.auth?.session?.authenticated,
    loggedinUserId: state.auth.session?.userinfo?._id,
    likedPosts: state.timelineNew.userFeed.likedPosts,
  };
}, {});

export const LikeFeed = connector(_LikeFeed);

function _LikeFeed({
  likedPosts,
  userId,
  loggedinUserId,
  loggedinUserInfo,
  wrapperStyle,
  isMyProfile,
  userNickname,
  authenticated,
}) {
  const dispatch = useDispatch();
  const [likedItems, setLikedItems] = useState(null);
  const [offset, setOffset] = useState(0);
  const [nbrRemoved, setNbrRemoved] = useState(0);
  const [userList, setUserList] = useState(null);
  const [likedComments, setLikedComments] = useState([]);
  const [sharedComments, setSharedComments] = useState([]);
  const [importStatus, setImportStatus] = useState(false);
  const [tellTwitter, setTellTwitter] = useState(false);
  const [endHours, setEndHours] = useState(48);
  const [loadingStatus, setLoadingStatus] = useState(false);

  const classes = useStyles();

  const api = Global.GetPortal().getAppService();

  const reachedEnd =
    Array.isArray(likedItems) &&
    (likedItems.length === 0 ||
      (likedItems.length + nbrRemoved) % MAX_BATCH_SIZE !== 0);

  const fetchAsyncStatus = async () => {
    if (userId === loggedinUserId) {
      let response;
      const config = {
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/u/user/${userId}/import`,
      };

      await GAxios(
        config,
        (res) => {
          if (!res?.data?.result?.status?.finished) {
            setImportStatus(true);
          }
          if (!res.data.result) {
            response = false;
          }
        },
        (err) => {
          console.error(err);
          response = false;
        },
      );
      return response;
    }
  };

  useEffect(() => {
    const fetchLikedPostsAndComments = async () => {
      const res = await api.fetchUserPosts(
        userId,
        offset * MAX_BATCH_SIZE,
        MAX_BATCH_SIZE,
        Date.now(),
        API.DIRECTION_FORWARD,
        null,
        "l", // l for liked
      );
      // set liked comments
      if (res.aux?.lks) {
        setLikedComments(res.aux.lks);
      }

      // set shared comments
      if (res.aux?.shrs) {
        setSharedComments(res.aux.shrs);
      }

      if (res?.aux?.removed) {
        setNbrRemoved(res?.aux?.removed);
      }

      if (res?.aux?.uinf) {
        setUserList(res.aux.uinf);
      }

      dispatch(
        setPostStatus({
          likes: res.aux?.lks,
          shares: res.aux?.shrs,
        }),
      );
      const items = res.getXPostItems(true);
      return items;
    };

    (async () => {
      const items = await fetchLikedPostsAndComments();
      if (offset === 0) {
        setLikedItems(items);
      } else {
        setLikedItems([...(likedItems || []), ...items]);
      }
    })();
  }, [userId, offset]);

  useEffect(() => {
    if (userId === loggedinUserId) {
      let now = Date.now();
      let hours = (now - loggedinUserInfo?.cdate) / (1000 * 60 * 60);
      setEndHours(48 - Math.floor(hours));
      let mins = (now - loggedinUserInfo?.cdate) / (1000 * 60);
      const imPost = localStorage.getItem(userId + "_imPost");
      if (hours < 48 && mins > 1 && imPost) {
        let res = fetchAsyncStatus();
      }
      if (mins < 1 && imPost) {
        setImportStatus(true);
      }
      if (hours < 24) {
        // setTellTwitter(true);
      }
    }
  }, [userId]);

  if (!likedItems) {
    return <GLoader wrapperStyle={wrapperStyle} />;
  }

  if (!likedItems.length) {
    return (
      <>
        {tellTwitter && !importStatus && (
          <div className={classes.claimTop}>
            <div className={classes.tellTwitter}>
              <Link
                className={classes.link}
                onClick={() => {
                  let text = t("getter_fe.auth.claim.tellTwitterText");
                  let href = window.location.origin + `/user/${userId}`;
                  window.open(
                    `https://twitter.com/intent/tweet?url=${href}&text=${text}`,
                    "_blank",
                  );
                }}
              >
                {t("getter_fe.auth.claim.tellTwitter")}
              </Link>
            </div>
          </div>
        )}
        <div className={classes.noDataWrapper}>
          <h5 className={classes.noDataTips}>
            <GTwemoji
              text={
                isMyProfile
                  ? t("getter_fe.profile.common.youHaventLiked")
                  : t("getter_fe.profile.common.otherHasntLiked", {
                      name: userNickname,
                    })
              }
            />
          </h5>
          <p className={classes.noDataSubtext}>
            {isMyProfile
              ? t("getter_fe.profile.common.youNoPostLikeOrCommentSubtext")
              : t("getter_fe.profile.common.otherNoPostLikeOrCommentSubtext")}
          </p>
        </div>
      </>
    );
  }

  return (
    <div className={classes.root}>
      {tellTwitter && !importStatus && (
        <div className={classes.claimTop}>
          <div className={classes.tellTwitter}>
            <Link
              className={classes.link}
              onClick={() => {
                let text = t("getter_fe.auth.claim.tellTwitterText");
                let href = window.location.origin + `/user/${userId}`;
                window.open(
                  `https://twitter.com/intent/tweet?url=${href}&text=${text}`,
                  "_blank",
                );
              }}
            >
              {t("getter_fe.auth.claim.tellTwitter")}
            </Link>
          </div>
        </div>
      )}
      {likedItems?.map((item, idx) => {
        const sharedPost = item.getLikedSharedPost && item.getLikedSharedPost();
        const thePosterId = item?.data?.activity?.tgt_oid;
        const replyToUserId = item?.data?.activity?.opuid;

        return item.data.activity.action === "likes_cm" ? (
          <div
            className={classes.commentContainer}
            style={{
              display:
                loggedinUserId === userId &&
                !likedPosts.includes(item.getPostId())
                  ? "none"
                  : "",
            }}
            key={idx}
          >
            {userList ? (
              <CommentFeedItem
                comment={item}
                usermap={userList}
                thePosterId={thePosterId}
                replyToUserId={replyToUserId}
                likedCommentsTab={likedComments}
                sharedCommentsTab={sharedComments}
                type={postConstants.TAB_LIKE}
              />
            ) : null}
          </div>
        ) : (
          <div
            style={{
              display:
                loggedinUserId === userId &&
                !likedPosts.includes(item.getPostId())
                  ? "none"
                  : "",
            }}
          >
            <PostFeedItem
              item={item}
              userId={userId}
              scene="profile"
              key={item.data.activity.pstid + idx}
              userId={item.getTargetId()}
              sharedObj={sharedPost}
              postId={item.getPostId()}
            />
          </div>
        );
      })}
      {reachedEnd ? null : (
        <Waypoint
          scrollableAncestor={window}
          onEnter={() => {
            if (!loadingStatus) {
              setOffset((oldOffset) => oldOffset + 1);
              setLoadingStatus(true);
            }
          }}
        />
      )}
    </div>
  );
}
