import React, {useState, useEffect} from "react";
import {useDispatch} from "react-redux";
import {Link, useHistory} from "react-router-dom";
import {Waypoint} from "react-waypoint";
import {createStyles, makeStyles} from "@material-ui/core/styles";
import API from "../../../core/API";
import {setPostStatus} from "src/app/components/timeline/store";
import {PostFeedItem} from "src/app/components/post/comps/PostFeedItem";
import {t} from "../../../i18n/utils";
import {PostComposer} from "src/app/components/post/comps/PostComposer";
import {GLoader} from "src/styles/components/GLoader";
import {GButton} from "src/styles/components/GButton";
import {connect} from "react-redux";
import {
  getPinnedPost,
  resetPinnedPost,
  resetRefreshProfileFeed,
  setPostCounts,
} from "src/app/components/timeline/store";
import {deletePinPost} from "src/app/components/auth/store";
import Global from "src/system/Global";
import GAxios from "src/util/GAxios";
import {divide} from "lodash-es";

const LOAD_MORE_CUTOFF_SIZE = 10;

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      marginBottom: theme.spacing(1),
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
    },
    claimTop: {
      width: "100%",
      height: "44px",
      marginBottom: "15px",
    },
    tellTwitter: {
      width: "100%",
      background: "rgba(47, 128, 237, 0.15)",
      alignItems: "center",
      textAlign: "center",
      height: "44px",
      paddingTop: "10px",
    },
    link: {
      fontFamily: "PingFang SC",
      fontStyle: "normal",
      fontWeight: "600",
      fontSize: "16px",
      lineHeight: "22px",
      color: "#2F80ED",
    },
    sync: {
      width: "100%",
      fontFamily: "PingFang SC",
      fontStyle: "normal",
      fontWeight: "600",
      fontSize: "16px",
      lineHeight: "22px",
      textAlign: "center",
      marginTop: "40px",
      marginBottom: "8px",
      color: "#333333",
    },
    sync2: {
      width: "100%",
      fontFamily: "PingFang SC",
      fontStyle: "normal",
      fontWeight: "normal",
      fontSize: "16px",
      lineHeight: "22px",
      textAlign: "center",
      marginBottom: "20px",
      color: "#999999",
    },
    noDataWrapper: {
      textAlign: "center",
      marginTop: "80px",
      // marginTop: "32px",
      marginBottom: "32px",
    },
    btn: {
      borderRadius: "100px",
      padding: "11px 0",
      borderColor: "#2F80ED !important",
      width: "119%",
      background: "white !important",
      "& span": {
        fontWeight: "600",
        fontSize: "20px",
        lineHeight: "28px",
        color: "#2F80ED",
      },
    },
    noDataSubtext: {
      color: theme.palette.text.gray,
    },
    noDataTips: {
      paddingTop: "50px",
      textAlign: "center",
      fontSize: "18px",
      lineHeight: "21px",
      color: "#000",
      marginBottom: theme.spacing(1),
    },
  }),
);

const MAX_BATCH_SIZE = 20;

const connector = connect(
  (state) => {
    return {
      loggedinUserId: state.auth.session?.userinfo?._id,
      loggedinUserInfo: state.auth.session?.userinfo,
      pinnedPosts: state.timelineNew?.pinnedPosts,
      refreshProfileFeed: state.timelineNew.refreshProfileFeed,
    };
  },
  {
    getPinnedPost,
    resetPinnedPost,
    deletePinPost,
    resetRefreshProfileFeed,
    setPostCounts,
  },
);

export const ProfileFeed = connector(_ProfileFeed);

function _ProfileFeed({
  userId,
  wrapperStyle,
  isMyProfile,
  getPinnedPost,
  pinnedPosts,
  resetPinnedPost,
  userInfo,
  loggedinUserInfo,
  deletePinPost,
  loggedinUserId,
  refreshProfileFeed,
  resetRefreshProfileFeed,
  forMedias,
  setPostCounts,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const [importStatus, setImportStatus] = useState(false);
  const [tellTwitter, setTellTwitter] = useState(false);
  const [endHours, setEndHours] = useState(48);
  const [offset, setOffset] = useState(0);
  const [isImagePinPost, setIsImagePinPost] = useState(false);
  const [loadingStatus, setLoadingStatus] = useState(false);
  const [getters, setGetters] = useState(null);
  const [sharedPost, setSharedPost] = useState(null);
  const [reachEnd, setReachEnd] = useState(false);
  const [canShowPinnedPosts, setCanShowPinnedPosts] = useState(false);

  const api = Global.GetPortal().getAppService();

  const noGetters =
    !getters?.length || getters?.every((getter) => !getter.getPost());

  let _pinnedPostsIds = userInfo && userInfo.getPinnedPosts();
  _pinnedPostsIds =
    _pinnedPostsIds && !Array.isArray(_pinnedPostsIds)
      ? _pinnedPostsIds.split(",")
      : _pinnedPostsIds;

  if (_pinnedPostsIds && _pinnedPostsIds.length > 0) {
    _pinnedPostsIds = _pinnedPostsIds.map((id) => {
      const indx = _pinnedPostsIds.indexOf(id);
      return {pstId: id, pstOrder: indx};
    });
  }

  const [pinnedPostsIds, setPinnedPostsIds] = useState(_pinnedPostsIds);

  const fetchPinnedPosts = async () => {
    if (!pinnedPostsIds) return;

    if (pinnedPostsIds && pinnedPostsIds.length > 0) {
      pinnedPostsIds.map(async (pId) => {
        const data = {api, postId: pId.pstId, pstOrder: pId.pstOrder};
        const res = await getPinnedPost(data);
        const post = res.payload && res.payload.getPost();

        if (post && (post.getImageURLs() || post.getVideoUrl()))
          setIsImagePinPost(true);

        // Unpin the pinned post
        if (!res?.payload && userId === loggedinUserId) {
          await deletePinPost({
            userId: loggedinUserId,
            postId: pId.pstId,
          });
        }
      });
    }
  };

  const removePost = (id) => {
    if (!id || getters.lenght > 0) {
      return;
    }

    let newPosts = getters.filter((post) => {
      const postId = post.getPostId();

      return postId !== id;
    });

    setGetters(newPosts);
  };

  const fetchSharedPost = async (postId) => {
    const sharedPost = await api.fetchPost(postId);
    if (sharedPost && !sharedPost.data.nfound) {
      setSharedPost(sharedPost);
      setCanShowPinnedPosts(true);
    }
  };

  const fetchGetters = async (userId, offset) => {
    let res = await api.fetchUserPosts(
      userId?.toLowerCase(),
      offset * MAX_BATCH_SIZE,
      MAX_BATCH_SIZE,
      Date.now(), //1613490289
      API.DIRECTION_FORWARD,
      null,
      null,
      forMedias,
    );

    const nbrRemoved = res.aux?.removed;

    dispatch(
      setPostStatus({
        likes: res.aux?.lks,
        shares: res.aux?.shrs,
      }),
    );

    const items = res.getXPostItems(true);

    let newItems;

    if (offset === 0) {
      newItems = [...items];
    } else {
      newItems = [...(getters || []), ...items];
    }

    if (Array.isArray(items) && items.length === 0) {
      setReachEnd(true);
    }

    setGetters(newItems);
    setLoadingStatus(false);
  };

  useEffect(() => {
    pinnedPosts.length <= 0 && fetchPinnedPosts();

    if (!reachEnd) {
      fetchGetters(userId, offset);
    }
  }, [userId, offset]);

  useEffect(() => {
    if (refreshProfileFeed) {
      setOffset(0);
      fetchGetters(userId, 0);
      setReachEnd(false);
      resetRefreshProfileFeed();
    }
  }, [refreshProfileFeed]);

  useEffect(() => {
    return () => {
      if (!forMedias) {
        resetPinnedPost();
      }
    };
  }, []);

  const fetchAsyncStatus = async () => {
    if (userId === loggedinUserId) {
      let response;
      const config = {
        method: "get",
        url: `${process.env.REACT_APP_API_URL}/u/user/${userId}/import`,
      };

      await GAxios(
        config,
        (res) => {
          if (!res?.data?.result?.status?.finished) {
            setImportStatus(true);
          }
          if (!res.data.result) {
            response = false;
          }
        },
        (err) => {
          console.error(err);
          response = false;
        },
      );
      return response;
    }
  };

  useEffect(() => {
    if (userId === loggedinUserId) {
      let now = Date.now();
      let hours = (now - loggedinUserInfo?.cdate) / (1000 * 60 * 60);
      setEndHours(48 - Math.floor(hours));
      let mins = (now - loggedinUserInfo?.cdate) / (1000 * 60);
      const imPost = localStorage.getItem(userId + "_imPost");
      if (hours < 48 && mins > 1 && imPost) {
        let res = fetchAsyncStatus();
      }

      if (mins < 1 && imPost) {
        setImportStatus(true);
      }
      if (hours < 24) {
        // setTellTwitter(true);
      }
    }
  }, [userId]);

  const updatePinnedPostsIds = (arr) => {
    let _pinnedPostsIds = arr.map((id) => {
      const indx = arr.indexOf(id);
      return {pstId: id, pstOrder: indx};
    });
    setPinnedPostsIds(_pinnedPostsIds);
    setGetters(getters);
  };

  useEffect(() => {
    if (!forMedias && pinnedPosts.length > 0) {
      if (pinnedPosts[0]?.aux?.shrdpst?._id) {
        const sharedPostId = pinnedPosts[0].aux.shrdpst._id;
        fetchSharedPost(sharedPostId);
      } else {
        setCanShowPinnedPosts(true);
      }
    } else {
      setCanShowPinnedPosts(false);
    }
  }, [pinnedPosts, forMedias]);

  const onPostSubmit = () => {
    history.push(`/dashboard`);
  };

  const postBox = (
    <PostComposer
      onSubmit={onPostSubmit}
      trigger={
        <GButton
          isBold
          // color="primary"
          variant="outlined"
          className={classes.btn}
        >
          {t("getter_fe.profile.common.createAPost")}
        </GButton>
      }
      isPopup={true}
    />
  );

  if (!getters) {
    return <GLoader wrapperStyle={wrapperStyle} />;
  }

  if (getters.length <= 0 && !isMyProfile) {
    return (
      <>
        <h5 className={classes.noDataTips}>
          {t("getter_fe.profile.common.otherHasntPosted", {
            name: userInfo.data.ousername || userInfo.data.username,
          })}
        </h5>
        <div className={classes.sync2}>
          {t("getter_fe.profile.common.otherNoPostLikeOrCommentSubtext")}
        </div>
      </>
    );
  }

  if (!getters?.length && !pinnedPosts?.length && isMyProfile && forMedias) {
    return (
      <>
        <h5 className={classes.noDataTips}>
          {t("getter_fe.profile.common.youNoPostMedia")}
        </h5>
        <div className={classes.sync2}>
          {t("getter_fe.profile.common.youNoPostLikeOrCommentSubtext")}
        </div>
      </>
    );
  }

  return (
    <div className={classes.root}>
      {tellTwitter && !importStatus && (
        <div className={classes.claimTop}>
          <div className={classes.tellTwitter}>
            <Link
              className={classes.link}
              onClick={() => {
                let text = t("getter_fe.auth.claim.tellTwitterText");
                let href = window.location.origin + `/user/${userId}`;
                window.open(
                  `https://twitter.com/intent/tweet?url=${href}&text=${text}`,
                  "_blank",
                );
              }}
            >
              {t("getter_fe.auth.claim.tellTwitter")}
            </Link>
          </div>
        </div>
      )}
      <div>
        {importStatus && !forMedias && (
          <div className={classes.claimTop}>
            <div className={classes.sync}>
              {t("getter_fe.auth.common.importingMSG1")}
            </div>
            <div className={classes.sync2}>
              {t("getter_fe.auth.common.importingMSG2", {hours: endHours})}
            </div>
          </div>
        )}
      </div>
      {((canShowPinnedPosts && !forMedias) || isImagePinPost) &&
        pinnedPosts &&
        pinnedPosts.map((post) => {
          return (
            <div
              key={"pinned-post-" + post.data.activity.pstid}
              style={{width: "100%"}}
            >
              {userInfo ? (
                <PostFeedItem
                  item={post}
                  scene="profile"
                  userId={userId}
                  sharedObj={sharedPost}
                  postId={post.getPostId()}
                  isPinnedPost={true}
                  updatePinnedPostsIds={updatePinnedPostsIds}
                  userInfoProfile={userInfo}
                />
              ) : null}
            </div>
          );
        })}

      {noGetters && !forMedias ? (
        <div className={classes.noDataWrapper}>
          {isMyProfile ? postBox : null}
        </div>
      ) : null}
      {userInfo &&
        getters?.map((item, idx) => {
          const sharedPost = item.getSharedPost();
          if (
            loggedinUserId === userId &&
            pinnedPostsIds &&
            pinnedPostsIds.length > 0 &&
            pinnedPostsIds[0].pstId === item.getPostId()
          ) {
            return null;
          } else {
            return (
              <div key={item.data.activity.pstid + idx} style={{width: "100%"}}>
                <PostFeedItem
                  item={item}
                  scene="profile"
                  type={forMedias && "for-medias"}
                  userId={item.getTargetId()}
                  sharedObj={sharedPost}
                  postId={item.getPostId()}
                  updatePinnedPostsIds={updatePinnedPostsIds}
                  userInfoProfile={userInfo}
                  removePostFromProfile={removePost}
                />

                {idx === getters?.length - LOAD_MORE_CUTOFF_SIZE ? (
                  <Waypoint
                    scrollableAncestor={window}
                    onEnter={() => {
                      if (!loadingStatus) {
                        setOffset((oldOffset) => oldOffset + 1);
                        setLoadingStatus(true);
                      }
                    }}
                  />
                ) : null}
              </div>
            );
          }
        })}
      {userInfo && (
        <Waypoint
          scrollableAncestor={window}
          onEnter={() => {
            if (!loadingStatus) {
              setOffset((oldOffset) => oldOffset + 1);
              setLoadingStatus(true);
            }
          }}
        />
      )}
    </div>
  );
}
