import {makeStyles} from "@material-ui/core";
import {ReactComponent as CommentIcon} from "src/assets/icons/ico-comment.svg";
import AppConsts, {UIStyleConsts} from "../../AppConsts";
import classnames from "classnames";
import {formatLongNumber} from "src/util/NumberUtil";
import {t} from "../../../i18n/utils";
import {useHistory} from "react-router";
import Global from "src/system/Global";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    "& .icon": {
      position: "relative",
      "& svg": {
        width: 18,
        height: 18,
        stroke: theme.palette.text.gray,
      },
      "& .tooltiptext": {
        position: "absolute",
        padding: "6px",
        background: theme.palette.background.gray,
        borderRadius: "2px",
        fontSize: "11px",
        color: theme.palette.text.disabled,
        visibility: "hidden",
        lineHeight: "8px",
        zIndex: 1,
        top: "100%",
        left: "50%",
        transform: "translate(-50%, 2px)",
        whiteSpace: "nowrap",
        "&.top": {
          transform: "translate(-50%, -52px)",
        },
      },
    },
    "& .count": {
      color: theme.palette.text.gray,
      fontWeight: 350,
      fontSize: "13px",
      [theme.breakpoints.only("xs")]: {
        fontSize: "11px",
      },
    },
    "&:hover": {
      "& .icon": {
        borderRadius: "100%",
        backgroundColor: "#EDF7FF",
        "& svg": {
          animation: "$fadeIn ease 0.3s",
          stroke: `${theme.palette.primary.main} !important`,
        },
        "& .tooltiptext": {
          visibility: "visible",
        },
      },
      "& .count": {
        color: `${theme.palette.primary.main} !important`,
      },
    },
    "&.white:hover": {
      "& .icon": {
        backgroundColor: "rgba(255,255,255,.2)",
        "& svg": {
          stroke: `#fff !important`,
        },
      },
      "& .count": {
        color: `#fff !important`,
      },
    },
  },
  "@keyframes fadeIn": {
    "0%": {
      stroke: theme.palette.text.secondary,
    },
    "100%": {
      stroke: theme.palette.primary.main,
    },
  },
}));

export const CommentPostButton = ({
  isPostDetail,
  count,
  tooltip,
  loggedIn,
  skin,
}) => {
  const classes = useStyles();
  const history = useHistory();
  loggedIn = !!Global.GetPortal().getAppService().getUserId();

  return (
    <div
      onClick={(e) => {
        if (!loggedIn) {
          e.stopPropagation();
          history.push(AppConsts.URL_LOGIN);
        }
      }}
      className={classnames(
        "comment-post-button",
        UIStyleConsts.POST_FEED_ITEM_BUTTON,
        classes.root,
        skin,
      )}
    >
      <div className="icon">
        <CommentIcon className="hollow" />
        <span className={`tooltiptext ${tooltip}`}>
          {t("getter_fe.post.button.reply")}
        </span>
      </div>

      <div className="text">
        {!isPostDetail && (
          <div className="count">{formatLongNumber(count || 0)}</div>
        )}
      </div>
    </div>
  );
};
