import React from "react";
import clsx from "clsx";
import {makeStyles} from "@material-ui/core/styles";
import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    position: "relative",
    minHeight: "100vh", // `calc(100vh - ${theme.mixins.header.height}px)`,
    flexWrap: "nowrap",
  },
  item: {
    position: "relative",
  },
  primaryColumn: {
    borderLeft: `1px solid  ${theme.palette.grey.A200}`,
    borderRight: `1px solid  ${theme.palette.grey.A200}`,
    backgroundColor: "white",
    boxShadow: "0px 4px 34px 5px rgba(236, 239, 245, 0.35);",
    [theme.breakpoints.up("md")]: {
      minWidth: "600px",
      maxWidth: "600px",
    },
    [theme.breakpoints.down("md")]: {
      flexShrink: 1,
    },
    [theme.breakpoints.up("lg")]: {
      flexShrink: 0,
    },
    [theme.breakpoints.down("sm")]: {
      maxWidth: "600px",
      width: "100%",
    },
    [theme.breakpoints.only("xs")]: {
      maxWidth: "100%",
    },
  },
  secondaryColumn: {
    height: "calc(100vh - 60px)",
    overflowY: "auto",
    [theme.breakpoints.down("md")]: {
      flexShrink: 0,
    },
  },
  rightMenu: {
    height: "auto",
    scrollbarWidth: "none",
    msOverflowStyle: "none",
    "&::-webkit-scrollbar": {
      display: "none",
    },
  },
}));

export const HomeMainContainer = (props) => {
  const classes = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up("md"));

  const getComponent = (key) => {
    return props.children.filter((comp) => {
      return comp.key === key;
    });
  };

  return (
    <Grid container className={clsx(classes.root, "primary")}>
      <Grid
        item
        xs={12}
        md={8}
        lg={7}
        className={clsx(classes.primaryColumn, classes.item)}
      >
        {getComponent("primary")}
      </Grid>
      {matches && (
        <Grid
          item
          md={4}
          lg={5}
          className={clsx(
            classes.secondaryColumn,
            classes.item,
            classes.rightMenu,
          )}
        >
          {getComponent("secondary")}
        </Grid>
      )}
    </Grid>
  );
};
