import React from "react";
import {makeStyles} from "@material-ui/core";
import {ReactComponent as LikeIcon} from "src/assets/icons/ico-like.svg";
import {ReactComponent as LikeActiveIcon} from "src/assets/icons/ico-liked.svg";
import AppConsts, {UIStyleConsts} from "../../AppConsts";
import classnames from "classnames";
import {t} from "../../../i18n/utils";
import {formatLongNumber} from "src/util/NumberUtil";
import {useHistory} from "react-router";
import Global from "src/system/Global";

const STATUS_LIKE = "y";
const STATUS_UNKNOWN = "u";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    "& .icon": {
      position: "relative",
      "& svg": {
        width: 18,
        height: 18,
        fill: "transparent",
        stroke: theme.palette.text.gray,
      },
      "& .tooltiptext": {
        position: "absolute",
        padding: "6px",
        background: theme.palette.background.gray,
        borderRadius: "2px",
        fontSize: "11px",
        color: theme.palette.text.disabled,
        visibility: "hidden",
        lineHeight: "8px",
        zIndex: 1,
        top: "100%",
        left: "50%",
        transform: "translate(-50%, 2px)",
        whiteSpace: "nowrap",
        "&.top": {
          transform: "translate(-50%, -52px)",
        },
      },
    },
    "& .count": {
      color: theme.palette.text.gray,
      fontWeight: 350,
      fontSize: 13,
      "&.active": {
        color: theme.palette.error.light,
      },
      [theme.breakpoints.only("xs")]: {
        fontSize: 11,
      },
    },
    "&:hover": {
      "& .icon": {
        borderRadius: "100%",
        backgroundColor: "#DD18180F",
        "& svg": {
          animation: "$fadeIn ease 0.3s",
          stroke: `${theme.palette.error.light} !important`,
        },
        "& .tooltiptext": {
          visibility: "visible",
        },
      },
      "& .count": {
        color: `${theme.palette.error.light} !important`,
      },
    },
    "&.white:hover": {
      "& .icon": {
        backgroundColor: "rgba(255,255,255,.2)",
        "& svg": {
          stroke: `#fff !important`,
        },
      },
      "& .count": {
        color: `#fff !important`,
      },
    },
  },
  "@keyframes fadeIn": {
    "0%": {
      stroke: theme.palette.text.gray,
    },
    "100%": {
      stroke: theme.palette.error.light,
    },
  },
}));

export const LikeButton = ({
  isPostDetail,
  likeStatus,
  count,
  objId,
  loading,
  loggedIn,
  tooltip,
  isComment,
  hideDropdown,
  setLikedCount,
  updateLikeStatus,
  updateCommentLikeStatus,
  skin,
}) => {
  const classes = useStyles();
  const history = useHistory();

  const isDisabled = loading || likeStatus === STATUS_UNKNOWN || !likeStatus;
  const isActive = likeStatus === STATUS_LIKE;

  const api = Global.GetPortal().getAppService();

  const onClick = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (loading) {
      return;
    }

    if (!loggedIn) {
      history.push(AppConsts.URL_LOGIN);
      return;
    }

    if (hideDropdown) {
      hideDropdown();
    }

    if (isActive) {
      // handle unlike

      setLikedCount(count - 1);

      try {
        if (isComment) {
          updateCommentLikeStatus("n");
          await api.userUnlikesComment(objId, null);
        } else {
          updateLikeStatus("n");
          await api.userUnlikesPost(objId, null);
        }
      } catch (err) {
        console.log(err);
      }
    } else {
      // handle like

      setLikedCount(count + 1);

      try {
        if (isComment) {
          updateCommentLikeStatus("y");
          await api.userLikesComment(objId, null);
        } else {
          updateLikeStatus("y");
          await api.userLikesPost(objId, null);
        }
      } catch (err) {
        console.log(err);
      }
    }
  };

  return (
    <div
      onClick={onClick}
      disabled={isDisabled}
      className={classnames(
        classes.root,
        "like-post-button",
        UIStyleConsts.POST_FEED_ITEM_BUTTON,
        {active: isActive},
        skin,
      )}
    >
      {isActive ? (
        <div className="icon">
          <LikeActiveIcon className={classes.likeActiveImage} />
          <span className={`tooltiptext ${tooltip}`}>
            {t("getter_fe.post.button.unlike")}
          </span>
        </div>
      ) : (
        <div className="icon">
          <LikeIcon className="hollow" />
          <span className={`tooltiptext ${tooltip}`}>
            {t("getter_fe.post.button.like")}
          </span>
        </div>
      )}

      <div className="text">
        {!isPostDetail && (
          <div className={classnames("count", isActive && "active")}>
            <span>{formatLongNumber(count || 0)}</span>
          </div>
        )}
      </div>
    </div>
  );
};
